import React from 'react'
import { BrowserRouter, Route, Routes } from 'react-router-dom'
// import CookPage from '../cook-WebPage/components/pages/home/CookPage'
import DashBoard from '../dashboard/home/DashBoard'
import About from '../dashboard/components/pages/About'
import Contact from '../dashboard/components/pages/Contact'
import Skill from '../dashboard/components/pages/Skill'
import Project from '../dashboard/components/pages/Project'



function RoutePath() {
    return (
        <div>
            <BrowserRouter>
                <Routes>
                    <Route path='/dashboard' index element={<DashBoard />} />
                    <Route path='/about-page' element={<About />} />
                    <Route path='/skill-page' element={<Skill />} />
                    <Route path='/project-page' element={<Project />} />
                    <Route path='/contact-page' element={<Contact />} />
                    {/* <Route path='/react-webPage'  element={<CookPage/>}/>    */}
                </Routes>
            </BrowserRouter>
        </div>
    )
}

export default RoutePath




